.container {
  max-width: 1320px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
  outline: 1px solid brown;
}

.section-h1 {
  font-size: clamp(1.063rem, 0.264rem + 3.55vw, 3.125rem);
  line-height: 1.2;
  letter-spacing: -0.02em;
  color: var(--white);
}

.section-h2 {
  font-size: clamp(1.063rem, 0.554rem + 2.26vw, 2.375rem);
  text-transform: uppercase;
}

.section-h3 {
  font-size: clamp(1.375rem, 1.133rem + 1.08vw, 2rem);
  text-transform: uppercase;
  color: var(--text);
}
.section-h3--ttn {
  text-transform: none;
}

.section-h4 {
  font-size: 22px;
  color: var(--text);
}

.section-h5 {
  font-size: 20px;
}

.btn {
  padding: 12px;
  font-size: clamp(0.938rem, 0.865rem + 0.32vw, 1.125rem);
  text-align: center;
  color: var(--white);
  line-height: normal;
  border-radius: 16px;
  background-color: #FF0000;
}

.btn-main {
  padding: 14px 19px 12px 48px;
  width: 132px;
  height: 48px;
  font-size: 16px;
  color: var(--white);
  background-color: var(--red);
  border-radius: var(--border-radius);
}
.btn-main__line {
  position: absolute;
  left: 22px;
  width: 18px;
  height: 2px;
  border-radius: var(--border-radius);
  background: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-main__line:nth-child(1) {
  top: 18px;
}
.btn-main__line:nth-child(2) {
  top: 23px;
}
.btn-main__line:nth-child(3) {
  top: 23px;
}
.btn-main__line:nth-child(4) {
  top: 28px;
}
.btn-main-open .btn-main__line:nth-child(1) {
  transform: scaleX(0);
}
.btn-main-open .btn-main__line:nth-child(2) {
  transform: rotate(45deg);
}
.btn-main-open .btn-main__line:nth-child(3) {
  transform: rotate(-45deg);
}
.btn-main-open .btn-main__line:nth-child(4) {
  transform: scaleX(0);
}

.btn-cart {
  border: 1px solid var(--red);
  border-radius: 60px;
  width: 146px;
  height: 42px;
  color: var(--white);
  background-color: #FF0000;
}

.btn-transparent {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: clamp(0.938rem, 0.768rem + 0.75vw, 1.375rem);
  border-radius: clamp(1.125rem, 0.81rem + 1.4vw, 1.938rem);
  background-color: transparent;
}
.btn-transparent--bg-white {
  color: var(--white);
  border: 2px solid var(--white);
}
.btn-transparent--bg-red {
  color: var(--red);
  border: 2px solid var(--red);
}

.see-all {
  width: clamp(9.625rem, 8.27rem + 6.02vw, 13.125rem);
  height: clamp(2.125rem, 1.835rem + 1.29vw, 2.875rem);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  font-size: clamp(0.938rem, 0.768rem + 0.75vw, 1.375rem);
  color: #da9000;
  border: 1px solid #ffe7b9;
  border-radius: clamp(0.75rem, 0.653rem + 0.43vw, 1rem);
  background: #fff8eb;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .see-all:hover {
    color: #ffffff;
    background-color: #ffa900;
  }
}
.see-all .catalog-menu__svg {
  display: inline-block;
  width: clamp(0.438rem, 0.317rem + 0.54vw, 0.75rem);
  height: clamp(0.5rem, 0.403rem + 0.43vw, 0.75rem);
  fill: currentColor;
  color: inherit;
}

.tabs-buttons__list {
  display: flex;
  gap: 15px;
}

.tabs-buttons {
  width: 221px;
  height: 87px;
  padding: 16px 22px;
  font-size: 20px;
  color: var(--cod-gray);
  border-radius: 16px;
  background-color: var(--serenade);
  border: 1px solid rgba(255, 169, 0, 0.45);
  text-align: left;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.tabs-buttons__text {
  display: block;
  margin-top: 6px;
  font-size: 16px;
}

.mixitup-control-active {
  border: 1px solid var(--yellow);
  background: var(--yellow);
  color: var(--white);
}

.header {
  padding-top: 23px;
  padding-bottom: 25px;
}
.header__top {
  display: flex;
  align-items: center;
}
.header__catalog-btn {
  margin-right: 26px;
}
.header__search-form {
  margin-right: 26px;
}
.header__actions {
  display: flex;
  gap: 14px;
}
.header__bottom {
  margin-top: 20px;
}

.catalog-menu__btn-main {
  position: relative;
}
.catalog-menu__ground {
  position: absolute;
  width: 53.33vw;
  padding: 41px 10px 28px 37px;
  top: 112px;
  left: 16px;
  border-radius: 14px;
  height: 403px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
  background: var(--white);
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.catalog-menu__list {
  width: 48%;
  height: 343px;
  padding-right: 23px;
  overflow-x: hidden;
  overflow-y: auto;
}
.catalog-menu__list::-webkit-scrollbar {
  width: 4px;
}
.catalog-menu__list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.catalog-menu__list::-webkit-scrollbar-track {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.catalog-menu .catalog-menu__ground.catalog-menu__ground--open {
  opacity: 1;
  visibility: visible;
}
.catalog-menu__item {
  position: relative;
}
.catalog-menu__item:not(:last-child) {
  margin-bottom: 26px;
}
.catalog-menu__svg {
  display: inline-block;
  fill: currentColor;
}
.catalog-menu__btn-secondary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  width: 100%;
  color: var(--cod-gray);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.catalog-menu__btn-secondary--active {
  color: var(--red);
}

.dropdown {
  position: fixed;
  width: 22.7%;
  height: 343px;
  top: 147px;
  left: 31%;
  background: var(--white);
  opacity: 0;
  visibility: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.dropdown::-webkit-scrollbar {
  width: 4px;
}
.dropdown::-webkit-scrollbar-thumb {
  background-color: #FF9E9E;
  border-radius: 9px;
}
.dropdown::-webkit-scrollbar-track {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.dropdown--active {
  opacity: 1;
  visibility: visible;
}
.dropdown__list {
  padding: 6px;
  width: 100%;
  height: 100%;
}
.dropdown__item {
  margin-bottom: 26px;
}
.dropdown__item--mb {
  margin-bottom: 42px;
}
.dropdown__link {
  color: var(--text);
}
.dropdown__link--title {
  font-weight: 600;
  color: var(--cod-gray);
}

.receiving {
  display: flex;
  color: var(--white);
}
.receiving__main::before {
  display: inline-block;
  content: "";
  background-color: rgba(255, 169, 0, 0.7);
  width: 65px;
  height: 48px;
  background: url(../images/icons/geolocation.svg) center center/24px 24px no-repeat rgba(255, 169, 0, 0.7);
  border-radius: 16px 0 0 16px;
}
.receiving__secondary {
  width: 230px;
  height: 48px;
  padding-top: 6px;
  background: var(--yellow);
  border-radius: 0 16px 16px 0;
}
.receiving__text {
  display: block;
  font-size: 14px;
}
.receiving__title {
  display: block;
  font-size: 16px;
}

.favorite {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  width: 50px;
  height: 48px;
  color: #e1e1e1;
  background-color: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .favorite:hover {
    border: 1px solid var(--yellow);
    background-color: #fff8eb;
    color: #ffa900;
  }
}

.hero {
  padding-top: clamp(1.375rem, 1.302rem + 0.32vw, 1.563rem);
}
.hero__inner {
  display: flex;
  gap: clamp(0.813rem, 0.546rem + 1.18vw, 1.5rem);
}
.hero-slider {
  max-width: 934px;
}
.hero-slide {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: clamp(0.625rem, 0.238rem + 1.72vw, 1.625rem);
}
.hero__title {
  max-width: clamp(11.563rem, 4.232rem + 32.58vw, 30.5rem);
  margin-bottom: clamp(1.125rem, 0.98rem + 0.65vw, 1.5rem);
}
.hero__content {
  padding-top: clamp(2.75rem, 1.153rem + 7.1vw, 6.875rem);
  padding-bottom: clamp(1rem, -0.718rem + 7.63vw, 5.438rem);
  padding-left: clamp(1.563rem, 0.909rem + 2.9vw, 3.25rem);
}
.hero__btn {
  width: clamp(8.125rem, 4.69rem + 15.27vw, 17rem);
  height: clamp(2.125rem, 1.593rem + 2.37vw, 3.5rem);
}
@media screen and (width < 768.99px) {
  .hero__btn--desktop {
    display: none;
  }
}
@media screen and (width > 768.99px) {
  .hero__btn--mobile {
    display: none;
  }
}
.hero-arrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background-color: #fff8eb;
  border-radius: 100%;
  border: 1px solid #FFF1D6;
  z-index: 2;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .hero-arrow:hover {
    border: 1px solid rgba(255, 169, 0, 0.45);
    background-color: #ffe8bc;
  }
}
@media screen and (width < 768.99px) {
  .hero-arrow {
    display: none;
  }
}
.hero-arrow--prev {
  left: 0;
}
.hero-arrow--next {
  right: 0;
}
.hero .swiper {
  margin: 0;
}
.hero .swiper-pagination {
  text-align: left;
}
.hero .swiper-horizontal > .swiper-pagination-bullets,
.hero .swiper-pagination-bullets.swiper-pagination-horizontal,
.hero .swiper-pagination-custom,
.hero .swiper-pagination-fraction {
  bottom: 35px;
  left: 52px;
}
@media screen and (width < 768.99px) {
  .hero .swiper-horizontal > .swiper-pagination-bullets,
  .hero .swiper-pagination-bullets.swiper-pagination-horizontal,
  .hero .swiper-pagination-custom,
  .hero .swiper-pagination-fraction {
    bottom: 0;
    left: 0;
