  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14px;
  height: 14px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.checkbox__castom-icon::after {
  content: "";
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background-color: #FF0000;
  opacity: 0;
}
.checkbox__icon-check {
  cursor: pointer;
}
.checkbox:checked + .checkbox__label .checkbox__castom-icon::after {
  opacity: 1;
}
.checkbox:checked + .checkbox__label .checkbox__castom-icon .checkbox__icon-check .checkbox__castom-icon::after {
  opacity: 1;
}
.checkbox__text {
  cursor: pointer;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
}

.footer-render {
